.logo {
    color: plum;
}

body {
    background-color: #100e17;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    text-transform: capitalize;
    border: none;
    outline: none;
    color: #ffffff;
}

header {
    height: 2vh;
    position: relative;
    z-index: 1;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.3);
}

header .container {
    padding: 1.2rem 9%;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

header .container .logo {
    font-size: 1.5rem;
}

header .container nav {
    display: flex;
    gap: 1rem;
    align-items: center;
}

header .container nav a {
    font-size: 1.2rem;
    padding: 10px 1rem;
    transition: 0.5s;
}

header .container nav a:hover {
    color: lightgray;
    /* Change to the desired color */
    transition: 0.5s;
    border-radius: 10px;
}
.logo{
    height: 70px;
}
header .container nav a.active {
    transition: 0.5s;
    border-radius: 10px;
}

header .container :is(.bar-icon, .close-icon) {
    font-size: 1.5rem;
    cursor: pointer;
    display: none;
}

header .container .overlay {
    position: fixed;
    inset: 0;
    z-index: 2;
    background: rgba(0, 0, 0, 0.5);
    display: none;
}

header .container .overlay.active {
    display: flex;
}

/* Media query */
@media (max-width: 435px) {
    header .container nav {
        font-size: 0.9rem;
        /* Adjust the font size as per your requirement */
    }

    header .container .bar-icon {
        display: block;
    }

    header .container .overlay {
        display: none;
    }

    header .container .overlay.active {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        gap: 1rem;
    }

    header .container .overlay a {
        font-size: 0.9rem;
        /* Adjust the font size as per your requirement */
        padding: 10px 1rem;
    }
}